<template>
  <div class="novel-transfer">
    <div v-if="$route.name === '小说推广链接'">
      <Input
        style="width: 400px;"
        size="large"
        type="text"
        v-model="searchUrl"
        placeholder="请输入查询链接"
      ></Input>
      <Button class="btn" :disabled="searchUrl === ''" type="success" size="large" @click="search">搜索</Button>
      <!-- <Button type="warning" size="large" @click="showList">刷新页面</Button> -->
      <Table class="spread-table" :columns="spreadHeader" :data="spreadData"></Table>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      spreadData: [],
      replaceUrl: '',
      searchUrl: "",
      spreadHeader: [
        {
          key: "_id",
          title: "id",
          align: "center"
        },
        {
          key: "url",
          title: "url",
          align: "center"
        },
        {
          key: "replaceUrl",
          title: "replaceUrl",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        render: h => {
                          return h("Input", {
                            props: {
                              value: that.replaceUrl,
                              autofocus: true,
                              placeholder: "请输入replaceUrl"
                            },
                            on: {
                              input: val => {
                                that.replaceUrl = val;
                              }
                            }
                          });
                        },
                        onOk: () => {
                          that.$axios.get('/novel_transfer/update', {
                            params: {_id: params.row._id, replaceUrl: that.replaceUrl}
                          }).then(res => {
                            that.$Message.info(res.data.success)
                            that.replaceUrl = ''
                            that.spreadData.splice(params.index, 1, res.data.data)
                          })
                        },
                        onCancel: () => {
                          that.replaceUrl = ''
                        }
                      });
                    }
                  }
                },
                "修改replaceUrl"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    // this.showList();
  },
  methods: {
    showList() {
      this.$axios.get("/novel_transfer").then(res => {
        this.spreadData = res.data.data;
      });
    },
    search() {
      this.$axios
        .get("/novel_transfer/find_one", {
          params: { searchUrl: this.searchUrl }
        })
        .then(res => {
          if (res.data.success) {
            this.$Message.info(res.data.success);
            this.spreadData = res.data.data;
            this.searchUrl = ''
          } else {
            this.$Message.info(res.data.error);
          }
        });
    }
  }
};
</script>


<style scoped>
.spread-table {
  margin: 30px 0;
}
.btn {
  margin: 0 30px 0 10px;
}
</style>
